    --background-clr: #fff;
    --nav-clr: #ECECEC;
    --font-clr: #424242;
    --primary-clr: #339DFF;
    --hover-clr: #E1E1E1;
    --low-priority: #FFDD2A;
    --medium-priority: #FFB46F;
    --high-priority: #FF6F6F;
}


/* Scroll Bar */
/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: var(--background-clr);
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: var(--nav-clr);
}


.dark-mode {
    --nav-clr: #0B0B10;
    --hover-clr: #000000;
    --background-clr: #1E1E25;
    --font-clr: #fff;
}

.dark-mode .icon {
    filter: brightness(1000);
}

@font-face {
    font-family: 'Roboto-Bold';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
    font-family: 'Roboto-Light';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

@font-face {
    font-family: 'Roboto-Regular';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

@font-face {
    font-family: 'Roboto-Slab';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    max-width: 100vw;
    background: var(--background-clr);
    color: var(--font-clr);
    font-family: 'Roboto-Regular';
    position: relative;
}

main {
    display: flex;
}

.icon {
    width: 20px;
}

li {
    list-style: none;
}

.close-dialog-btn {
    position: absolute;
    right: 20px;
    top: 20px;
    width: 25px;
    cursor: pointer;
}

dialog {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: none;
    padding: 40px;
    width: min(90%, 500px);
    border-radius: 8px;
    outline: none;
    background: var(--background-clr);
    color: var(--font-clr);
}

dialog::backdrop {
    background: black;
    opacity: .6;
}

.dialog-input {
    font-size: 1rem;
    border: none;
    background: var(--nav-clr);
    padding: 10px 10px;
    border-radius: 8px;
    font-family: "Roboto-Regular";
    color: var(--font-clr);
}

.dialog-btn {
    background: var(--primary-clr);
    color: white;
    width: 100%;
    padding: 12px;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 1rem;
    border-radius: 8px;
}

.dialog-title {
    font-family: "Roboto-Bold";
    margin-bottom: 20px;
