___CSS_LOADER_EXPORT___.push([module.id, `.todos-wrapper {
    display: block;
    margin-top: 24px;
    width: 100%;
}

.todo {
    border-bottom: 2px solid rgba(78, 78, 78, 0.196);
    padding-block: 20px;
}

.todo-info {
    display: flex;
    justify-content: space-between;
}

.todo-description {
    padding-top: 20px;
    padding-left: 40px;
    height: fit-content;
    transition: .4s;
    opacity: .8;
}

.hide-description {
    padding-top: 0px;
    height: 0px;
    overflow: hidden;
}

.todo-header,
.todo-footer {
    display: flex;
    align-items: center;
    gap: 20px;
}

.todo-footer {
    font-family: 'Roboto-Light';
}

.todo-project {
    text-transform: capitalize;
}

.todo-checkbox {
    appearance: none;
    background: var(--nav-clr);
    height: 20px;
    width: 20px;
    border-radius: 2px;
}

.todo-checkbox:checked {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.todo-priority {
    /* TODO: Remove this */
    background: red;
    height: 12px;
    width: 12px;
    border-radius: 100%;
}

.todo-utility-wrapper {
    display: flex;
    gap: 20px;
    align-items: center;
    width: 0;
    overflow: hidden;
    transition: .4s;
}

.todo:hover .todo-utility-wrapper {
    width: 80px;
}


.todo-utility-wrapper *:hover {
    cursor: pointer;
    filter: contrast(10);
}`, "",{"version":3,"sources":["webpack://./src/css/todo.css"],"names":[],"mappings":"AAAA;IACI,cAAc;IACd,gBAAgB;IAChB,WAAW;AACf;;AAEA;IACI,gDAAgD;IAChD,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,mBAAmB;IACnB,eAAe;IACf,WAAW;AACf;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,gBAAgB;AACpB;;AAEA;;IAEI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,0BAA0B;AAC9B;;AAEA;IACI,gBAAgB;IAChB,0BAA0B;IAC1B,YAAY;IACZ,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,yDAAiD;AACrD;;AAEA;IACI,sBAAsB;IACtB,eAAe;IACf,YAAY;IACZ,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,QAAQ;IACR,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,WAAW;AACf;;;AAGA;IACI,eAAe;IACf,oBAAoB;AACxB","sourcesContent":[".todos-wrapper {\n    display: block;\n    margin-top: 24px;\n    width: 100%;\n}\n\n.todo {\n    border-bottom: 2px solid rgba(78, 78, 78, 0.196);\n    padding-block: 20px;\n}\n\n.todo-info {\n    display: flex;\n    justify-content: space-between;\n}\n\n.todo-description {\n    padding-top: 20px;\n    padding-left: 40px;\n    height: fit-content;\n    transition: .4s;\n    opacity: .8;\n}\n\n.hide-description {\n    padding-top: 0px;\n    height: 0px;\n    overflow: hidden;\n}\n\n.todo-header,\n.todo-footer {\n    display: flex;\n    align-items: center;\n    gap: 20px;\n}\n\n.todo-footer {\n    font-family: 'Roboto-Light';\n}\n\n.todo-project {\n    text-transform: capitalize;\n}\n\n.todo-checkbox {\n    appearance: none;\n    background: var(--nav-clr);\n    height: 20px;\n    width: 20px;\n    border-radius: 2px;\n}\n\n.todo-checkbox:checked {\n    background-image: url(\"../assets/icons/tick.svg\");\n}\n\n.todo-priority {\n    /* TODO: Remove this */\n    background: red;\n    height: 12px;\n    width: 12px;\n    border-radius: 100%;\n}\n\n.todo-utility-wrapper {\n    display: flex;\n    gap: 20px;\n    align-items: center;\n    width: 0;\n    overflow: hidden;\n    transition: .4s;\n}\n\n.todo:hover .todo-utility-wrapper {\n    width: 80px;\n}\n\n\